section#contacts{
    padding-bottom: 50px;
    .section-content{
        @include row-flex();
    }

    .contacts-list{
        @include col();
        @include size(6);
        @include size-sm(12);

        display: flex;
        flex-direction: column;
        align-items: flex-end;
        padding-right: 50px;
        padding-top: 25px;

        @include sm-block(){
            padding-top: 0;
            padding-right: 0;
            align-items: flex-start;
            margin-bottom: 30px;
        }

        li{
            display: flex;
            align-items: center;
            flex-direction: row;
            font-size: 18px;
            // font-weight: bold;
            @include nlc(){
                margin-bottom: 10px;
            }

            &:before{
                position: relative;
                top: 3px;
                margin-right: 10px;
            }
        }
        .email{
            &:before{
                content: url("data:image/png;base64,");
            }
        }
        
        .phone{
            &:before{
                content: url('data:image/png;base64,')
            }
        }
    }
    
    .contacts-form{
        @include col();
        @include size(6);
        @include size-sm(12);
        
        form{
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            &:valid{
                & [type="submit"]{
                    border-color: #000;
                }
            }
            
            h4{
                display: block;
                margin-bottom: 10px;
            }

            input.input{
                width: 260px;
                // @include sm-block(){
                //     width: 50%;
                // }
                @include xs-block(){
                    width: 100%;
                }
            }
            .input{
                width: 100%;
                &:not(:last-child){
                    margin-bottom: 15px;
                }
            }
            
        }
    }
}