section#jumbotron{
    position: relative;
    padding-top: 64px;
    // height: calc(100vh - 64px);
    height: 100vh;
    overflow: hidden;

    #particles{
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
    }

    .container{
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .jumbotron-content{
        // height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 10;
        text-transform: uppercase;
        font-family: $roboto_c;
        text-align: center;

        span{
            display: inline-block;
        }

        h1{
            font-size: 55px;
            margin-bottom: 20px;
            @include xs-block(){
                font-size: 30px;
            }
        }
        p{
            font-size: 18px;
            line-height: 32px;
            @include xs-block(){
                font-size: 14px;
                line-height: 24px;
            }
            span{
                display: inline-block;
            }
        }
    }
}