@function calculateRem($size) {
  $remSize: $size / 16px;
  @return $remSize * 1rem;
}

@mixin lhCrop($line-height, $capital-letter: 0.5) {
  &::before {
    content: '';
    display: block;
    height: 0;
    width: 0;
    margin-top: calc((#{$capital-letter} - #{$line-height}) * 0.5em);
  }
}

@mixin btn($height, $borderwidth, $borderradius, $bordercolor, $color, $fontweight, $fontsize, $minwidth, $bg-color, $bg-img){
  height: $height;
  min-width: $minwidth;
  border: $borderwidth solid $bordercolor;
  font-weight: $fontweight;
  font-size: $fontsize;
  color: $color;
  line-height: 1;
  border-radius: $borderradius;
  position: relative;
  padding: ($height - $fontsize - 2 * $borderwidth) / 2 5px;
  background-color: $bg-color;
  background-image: $bg-img;
}

@mixin btn-hover ($color, $bg-color, $bg-img) {
  &:hover {
    color: $color;
    background-color: $bg-color;
    background-image: $bg-img;
  }
}

@mixin btn-click ($color, $bg-color, $bg-img) {
  &:focus {
    color: $color;
    background-color: $bg-color;
    background-image: $bg-img;
  }
}

@mixin placeholder(){
  &::placeholder{
    @content;
  }
  &::-webkit-input-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
}

@mixin nlc{
  &:not(:last-child){
    @content;
  }
}