h2.heading{
    font-size: 30px;
    text-transform: uppercase;
    text-align: center;
    position: relative;
    display: inline-block;
    margin: 0 auto;
    margin-bottom: 75px;
    margin-top: 40px;
    @include sm-block(){
        margin-bottom: 55px;
        margin-top: 30px;
    }
    &:before{
        content: "";
        display: block;
        width: 50%;
        height: 2px;
        background-color: #000;
        position: absolute;
        bottom: -15px;
        left: 25%;
    }
}

section{
    @include nlc(){
        border-bottom: 1px solid #B3B3B3;
    }
    .container{
        display: flex;
        flex-direction: column;
    }
    // .heading{
    //     margin-top: 50px;
    // }
    p{
        line-height: 22px;
    }
}


.input{
    border: 2px solid #a2a2a2;
    padding: 10px;
    font-size: 14px;
    font-family: $roboto;
    color: #000;
    // cursor: pointer;
    
    @include placeholder{
        color: #616161;
        
    }
    &:valid{
        border-color: #000;
    }
    &:focus{
        border-color: #000;
        @include placeholder{
            color: #a0a0a0;
            
        }
    }
}

input.input{
}

textarea.input{
    max-width: 100%;
    min-height: 150px;
}


.button{
    border: 2px solid #000;
    background-color: #fff;
    font-size: 15px;
    padding: 10px;
    font-family: $roboto;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    z-index: 0;
    transition: .4s;
    transition-property: color, border-color;

    span.fill{
        position: absolute;
        display: block;
        background-color: #000;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        transition: width 0.3s ease-in-out, height 0.3s ease-in-out;
        z-index: -1;
    }

    &[type="submit"]{
        border-color: #a2a2a2;
        &:hover{
            border-color: #000;
        }
    }
}