header.header{
    border-bottom: 1px solid #000;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
    background-color: #fff;
    .header-container{
        // padding: 0 15px;
        padding-top: 10px;
        padding-bottom: 10px;
        display: flex;
        align-items: center;
        // max-width: 1250px;
        // margin: 0 auto;
    }
    .brand{
        text-transform: uppercase;
        font-family: "Roboto Condensed";
        font-weight: 700;
        padding: 5px 10px;
        font-size: 22px;
        border: 3px solid black;
        margin-right: 10px;
        transition: .7s;
        transition-property: color, background-color, border-color, font-size;
        text-shadow: -2px 2px 1px #00000026;
        position: relative;
        z-index: 2;
        background-color: #fff;
        @include xs-block(){
            font-size: 16px;
        }
        &:hover{
            background-color: black;
            color: #fff;
            & + .branding{
                transform: translateX(0);
                opacity: 1;
            }
        }

        &.small{
            font-size: 16px;
        }
    }
    
    .branding{
        transition: .5s;
        transition-property: transform, opacity;
        transform: translateX(-120%);
        opacity: 0;
        position: relative;
        z-index: 1;
        font-family: $roboto_c;
        font-size: 14px;
        margin-right: auto;
        align-self: flex-start;
        margin-top: 2px;
        @include xs-block(){
            display: none;
        }
    }

    .burger{
        cursor: pointer;
        display: none;
        flex-direction: column;
        justify-content: space-between;
        width: 40px;
        height: 26px;
        align-items: flex-end;
        cursor: pointer;
        margin-left: auto;
        @include sm-block(){
            display: flex;
        };

        span{
            display: block;
            background-color: #000;
            height: 3px;
            width: 100%;
            transition: .5s;
            transition-property: opacity, transform;
            &:nth-child(2){
                width: 80%;
            }
        }
    }

    .header-nav{
        
        font-family: $roboto;
        font-size: 15px;
        transition: .5s;
        transition-property: transform;
        @include sm-block(){
            transform: translateY(-100%);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            min-height: 100vh;
            z-index: 9998;
            background-color: rgba(0, 57, 84, 0.95);
            display: flex;
            align-items: center;
            justify-content: center;
            &.show{
                transform: translateY(0);

                & + .burger{ 
                    z-index: 9999;
                    span{
                        
                        background-color: #fff;

                        &:nth-child(1){
                            transform: rotate(45deg) translateX(12px) translateY(5px);
                        }
                        &:nth-child(2){
                            opacity: 0;
                        }
                        &:nth-child(3){
                            transform: rotate(-45deg) translateX(10px) translateY(-5px);
                        }
                    }
                }
            }

            ul{
                text-align: center;
                flex-direction: column;
                font-size: 25px;
                color: #fff;
                text-transform: uppercase;
                font-family: $roboto_c;
                
            }
        }
        
        ul{
            display: flex;    
            li{
                &:not(:last-child){
                    margin-right: 30px;
                    @include sm-block(){
                        margin-right: 0;
                        margin-bottom: 25px;
                    }
                }
                a{
                    transition: color .2s ease-out;
                    &:hover{
                        // color: #ff4e4e;
                        color: #ff4c44;
                        @include sm-block(){
                            color: #b9b9b9;
                        }
                    }
                }
            }
        }
    }
}    

